  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  background: linear-gradient(to right, #000000, #16140c);
}

header {
  height: 70px;
  display: flex;
  justify-content: space-between;
  background: var(--header-bg-color);
  box-shadow: 
  inset 0 -2px 10px rgba(0, 0, 0, 0.579); /* Stronger shadow at the bottom */
}

.header-left {
  display: flex;
  margin-left: 50px;
  width: 350px;
}

.logo-header {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
