  width: 100px;
  font-size: 1.3rem;
  font-weight: bold;
  background-color: transparent;
  cursor: pointer;
  transition: transform 0.3s ease-out;
  border: none;
}

button:hover {
  transform: scale(1.15);
  span {
    border-bottom: 2px solid black;
  }
}

.highlight {
  border-bottom: 2px solid black;
}

