}

.card-3 .img {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom -30px right 0;
}

.card-4 .img {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
