ize: 1rem;\n  display: flex;\n  height: 40px;\n  bottom: 0; /* Align to the bottom of the container */\n  left: 0; /* Ensure it stretches across the container */\n  width: 100%; /* Full width of the container */\n  justify-content: center;\n  align-items: center;\n  display: flex;\n  cursor: pointer;\n}\n\na {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  text-decoration: none;\n  color: rgba(255, 255, 255, 0.573);\n}\n\na:visited {\n  color: rgba(255, 255, 255, 0.573);\n}\n\n.footer-img {\n  background: url(img/github.svg);\n  background-size: cover;\n  background-position: center;\n  border-radius: 50%;\n  background-color: aliceblue;\n  width: 25px;\n  height: 25px;\n}\n\n.headline {\n  font-size: 3rem;\n  width: 80%;\n  text-align: center;\n}\n\n.description {\n  font-size: 1.4rem;\n  outline: 1px solid black;\n  width: 90%;\n  text-align: center;\n}\n\n.description > em {\n  color: rgb(215, 245, 125);\n  font-size: 1.45rem;\n}\n\n.book-btn {\n  border: 1px solid rgb(217, 242, 245);\n  font-size: 1.4rem;\n  color: rgb(217, 242, 245);\n  font-weight: normal;\n  width: 200px;\n  height: 60px;\n  border-radius: 50px;\n  background-color: transparent;\n  cursor: pointer;\n  transition: transform 0.3s ease-out;\n}\n\n/* Menu Content Style */\n\n.menu-container {\n display: grid;\n grid-template-columns: repeat(3, 500px);\n grid-template-rows: repeat(2, minmax(400px, 1fr));\n gap: 100px;\n justify-content: center;\n}\n\n.menu-container > div {\n  border-radius: 50px;\n  display: flex;\n  flex-direction: column;\n  box-shadow: 0px 0px 8px 2px rgba(186, 238, 246, 0.526); \n  transition: transform 0.3s ease-out;\n}\n\n.menu-container > div:hover {\n  transform: scale(1.05);\n}\n\n.img {\n  border-radius: 50px 50px 0 0;\n  height: 75%;\n  box-shadow: inset rgb(3, 3, 3) 5px 5px 60px -2px; \n}\n\n.card-1 .img {\n  background: url(img/filet-mignon.jpg);\n  background-size: cover;\n  background-position: top -10px right 0;\n  background-repeat: no-repeat; \n}\n\n.card-2 .img {\n  background: url(img/ribeye-steak.jpg);\n  background-size: cover; \n  background-repeat: no-repeat; \n}\n\n.card-3 .img {\n  background: url(img/steak-frites.jpg);\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: bottom -30px right 0;\n}\n\n.card-4 .img {\n  background: url(img/steak-tartare.jpg);\n  background-size: 500px auto;\n  background-repeat: no-repeat;\n  background-position: bottom -130px right 0;\n}\n\n.card-5 .img {\n  background: url(img/surf-turf.jpg);\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: bottom -175px right 0;\n}\n\n.card-6 .img {\n  background: url(img/tomahawk-steak.jpg);\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: bottom -20px right 0;\n}\n\n.food-title {\n  position: relative;\n  text-align: center;\n  padding: 10px 0;\n  height: 10%;\n  font-size: 1.3rem;\n  background-color: #000000;\n  border-top: 2px solid rgba(178, 240, 248, 0.51);\n  color: var(--home-font-color);\n}\n\n.desc {\n  height: 15%;\n  font-size: clamp(1rem, 0.5vw, 1.3rem);\n  padding: 2px 25px 5px 25px;\n  text-align: center;\n  border-radius: 0 0 50px 50px;\n  word-wrap: break-word;\n  background-color: #000000;\n  color: var(--home-font-color);\n}\n\n.menu-title {\n  color: var(--home-font-color);\n  text-align: center;\n  font-size: 3.3rem;\n  padding: 30px 0 30px 0;\n}\n\n/* Contact style */ \n\n.contact-container {\n  position: relative;\n  display: flex;\n  justify-content: center;\n  padding-top: 120px;\n}\n\n.contact-bg-img {\n  position: absolute;\n  background: url(img/contact-bg.jpg);\n  background-repeat: no-repeat;\n  background-size: cover;\n  height: 65vh;\n  width: 100%;\n  z-index: -1;\n}\n\n.contact-sec {\n  background-color: #16140ce9;\n  width: 40%;\n  height: 65vh;\n  border-radius: 50px;\n  border: 1px solid rgba(209, 248, 245, 0.492);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.contact-title {\n  font-size: 3.3rem;\n  padding: 50px 0 50px 0;\n  color: var(--home-font-color);\n}\n\n.info-container {\n  display: flex;\n  flex-direction: column;\n  align-content: center;\n  width: 700px;\n  gap: 40px;\n  color: var(--home-font-color);\n  padding-left: 75px;\n  font-size: 1.8rem;\n  padding-bottom: 70px;\n}\n\n.info {\n  line-height: 2.5rem;\n}\n\n.follow-container {\n  width: 700px;\n  padding-top: 30px;\n  text-align: center;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 40px;\n}\n\n.follow-container > div {\n  transition: transform 0.2s ease-out;\n  cursor: pointer;\n}\n\n\n.follow-container > div:hover {\n  transform: scale(1.05);\n}\n\n.follow-container > div:active {\n  transform: scale(1.1);\n}\n\n.insta {\n  background: url(img/insta.svg);\n  background-repeat: no-repeat;\n  width: 60px;\n  height: 60px;\n  background-size: cover;\n}\n\n.x {\n  background: url(img/x.svg);\n  background-repeat: no-repeat;\n  width: 60px;\n  height: 60px;\n  background-color: rgb(255, 255, 255);\n  border-radius: 13px;\n}\n\n.facebook {\n  background: url(img/facebook.svg);\n  width: 64px;\n  height: 64px;\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-color: rgb(255, 255, 255);\n  border-radius: 12px;\n  overflow: hidden;\n}\n\n.follow-us {\n  color: rgb(250, 242, 201);\n  font-size: 1.8rem;\n}\n"],"sourceRoot":""}]);
